.drop-container
{
	display: block;
}

.navbar-inverse
{
	border-color:transparent;
	color:black;
	background-color:transparent;
}
.navbar-inverse .navbar-brand
{
	color: black !important;
	font-size: 3em;
	margin-top:.3em;
}
.navbar-inverse .navbar-brand span
{
	color: #DA6A3C !important;
}
.navbar-inverse .navbar-toggle .icon-bar
{
	background-color:black;
}
.navbar-inverse .navbar-nav>li>button
{
	color: black!important;
	font-weight:600;
	padding-top: 13.9px;
}
.navbar-inverse .navbar-nav>li>button:hover
{
	color: #DA6A3C!important;
}
.navbar-inverse .navbar-nav>li>a
{
	color: black!important;
	font-weight:600;
}
.navbar-inverse .navbar-nav>li>a:hover
{
	color: #DA6A3C!important;
}
.navbar-nav
{
	margin:15px 0px 0px 50px ;
}
.navbar-nav li
{
	padding:0px  15px 0px 0px;
}

/*Me css*/
.table-responsive 
{
	padding-bottom:13.2em;
}
.btn-link:hover
{
	text-decoration: none;
}
.btn-link:focus
{
	
	outline:0px auto -webkit-focus-ring-color;
	outline-offset: 0px;
	text-decoration: none;
}
.btn:active:focus{
	outline:0px auto -webkit-focus-ring-color;
	outline-offset: 0px;
	text-decoration: none;
}
.active
{
	display: block !important;
}
.sub-menu
{
	display: none;
   
}
.sub-menu a
{
	color:black;
}
#drop-down
{
	display: none;
}
.col-xs-12-me
{	
	display: table;
	background: red;
	position: relative;
	z-index: 999999;
}
.col-md-3-me
{	
	display: table;
	background: white;
	position: relative;
	z-index: 999999;
}
.col-md-9-me{
	background: red;
	width: 66.7%;
	display: table;
	background: red;
	position: relative;
	z-index: 999999;
}
.drop-main-list 
{
	border-top: 1px solid #ddd;
	border-bottom: 1px solid #ddd;
}
.sub-main-list li
{
	list-style: none;
	margin-top:1em;
}
.sub-main-list li a
{
	color: black;
}
.drop-main-list li
{
	list-style: none;
	margin-top:1em;
	
}
.drop-main-list li button
{
	color: black;
}
.table>tbody>tr>td
{
	border:0px;
}

/*Media Queries*/

@media screen and (min-width:320px){
	.all-close
	{
		display: none;

	}
.navbar-inverse .navbar-brand
{
	color: black !important;
	font-size: 2em;
	margin-top:.3em;
}
.sub-menu
{
	display: none;
	margin-top: -23em;
	background: white;
	z-index: 99999999;
	
}
#drop-down{
	margin-top:-22.75em;
	left: 0%!important;
}
.col-xs-12-me{
	width: 100%;	
	position: absolute;
}
.table tbody tr td
{
  display: block;
}

}

@media only screen and (min-width:768px)and (max-width: 990px){
.navbar-inverse .navbar-brand
{
		
	margin-top:0.6em;
}
.navbar-nav li
{
	padding:0px  0px 0px 0px;
}
	.navbar-nav{
		margin:-3.8em 0em 0em 12.9em ;
	}
.sub-menu
{
	display: none;
	margin-top: -2em;
	background: white;
	z-index: 99999999;
	
}
#drop-down{
	margin-top:-2em;
	left:0% ;
	
}
#sub-close
	{
		display: block;
	}
.col-xs-12-me{
	width: 100%;	
	position: absolute;
}

}

@media only screen and (min-width:992px) {

.navbar>.container-fluid .navbar-brand
{
	margin-left:0px;

}
.navbar-inverse .navbar-brand
{
	
	font-size: 3.2em;
	margin-top:.3em;
}
	.navbar-nav{
		margin:-50px 0px 0px 24em ;
		
	}
	.navbar-nav li
	{
		padding: 0px  6px 0px 0px;
	}
	#main-close
	{
		display: none;
	}
	#sub-close
	{
		display: none;
	}
.sub-menu
{
	display: none;
	margin-top: 0em;
	background: white;

	left: 44.3%;
	border:1px solid #ddd;
	height:79.7%;

	width: 49.7%;
	position: absolutes;
	z-index: 9999;
	
	
}
.drop-main-list 
{
	padding-bottom: 2em;
     margin-top:-2em;
	border-top: 0px solid #ddd;
	border-bottom: 0px solid #ddd;
}
#drop-down{
	margin-top:0em;
	border-left: 0px;
	border:1px solid #ddd;
	left: 25% !important;
}

.table tbody tr td
{
  display:table-cell;
}
	.col-md-3-me{
	background: white;
	width: 10.3%;
	display: block;
	position:absolute;
	z-index: 9999;
}
	
.drop-main-list li:nth-child(1)
{
	list-style: none;
	margin-top:2em;
	margin-bottom: -2.6em;
	
}
.drop-main-list li
{
	list-style: none;
	margin-top:2em;
	margin-bottom: -2.6em;

	
}
.all-close
{
	display: block;
	padding:2px 12px 0 0px;
	float: right;
}

}
@media only screen and (min-width:1024px){
	.navbar-nav
	{
		margin:-3.8em 0em 0em 24.9em !important;
	}
	.sub-menu
	{
	display: none;
	margin-top: 0em;
	background: white;
	z-index: 99999999;
	left: 34.6%!important;
	border-top-right-radius:5px;
	border-bottom-right-radius:5px;
	border:1px solid #ddd;
	border-left: 0px;
	

	-webkit-box-shadow: 6px 3px 11px -3px grey;
	

	        box-shadow: 6px 3px 11px -3px grey;
   
		
	}
	.col-md-3-me{
	background: white;
	width: 10.3%;
	height:100%;
	display: block;
	position:absolute;
	z-index: 9999;
	left:25%;
}
}

